import html2canvas from "html2canvas"
import jsPDF from "jspdf"

interface InvoiceData {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

const COMPANY_INFO = {
  name: "(주)GS",
  address: "서울특별시 강남구 논현로 508 GS타워",
  bank: "우리은행 982-018207-01-002 ㈜지에스",
}

function formatCurrency(amount: number): string {
  return new Intl.NumberFormat("ko-KR").format(amount) + "원"
}

function createInvoiceHTML(record: InvoiceData, index: number, total: number): string {
  const today = new Date().toLocaleDateString("ko-KR", {
    year: "numeric",
    month: "long",
    day: "numeric",
  })

  return `
    <div style="width: 210mm; height: 297mm; padding: 20mm; background: white; font-family: 'Noto Sans KR', 'Malgun Gothic', sans-serif; box-sizing: border-box; position: relative;">
      <!-- Header -->
      <div style="display: flex; justify-content: space-between; align-items: flex-start; margin-bottom: 10mm; padding-bottom: 5mm; border-bottom: 2px solid #5B4FFF;">
        <h1 style="font-size: 32px; font-weight: bold; color: #5B4FFF; margin: 0;">INVOICE</h1>
        <div style="text-align: right; font-size: 12px; color: #666;">
          <div style="font-weight: 600; margin-bottom: 4px;">발행일</div>
          <div>${today}</div>
        </div>
      </div>

      <!-- From and To Information -->
      <div style="display: flex; justify-content: space-between; margin-bottom: 15mm;">
        <div style="flex: 1;">
          <h3 style="font-size: 11px; font-weight: bold; color: #666; margin: 0 0 8px 0;">FROM</h3>
          <div style="font-size: 13px;">
            <div style="font-weight: 600; margin-bottom: 4px; font-size: 15px;">${COMPANY_INFO.name}</div>
            <div style="color: #666; margin-bottom: 3px;">${COMPANY_INFO.address}</div>
            <div style="color: #666; margin-top: 6px;">
              <span style="font-weight: 500;">입금계좌:</span> ${COMPANY_INFO.bank}
            </div>
          </div>
        </div>
        <div style="flex: 1;">
          <h3 style="font-size: 11px; font-weight: bold; color: #666; margin: 0 0 8px 0;">TO</h3>
          <div style="font-size: 13px;">
            <div style="font-weight: 600; margin-bottom: 4px; font-size: 15px;">${record.company}</div>
            <div style="color: #666; margin-bottom: 3px;">
              <span style="font-weight: 500;">담당자:</span> ${record.contactName}
            </div>
            <div style="color: #666;">${record.contactEmail}</div>
          </div>
        </div>
      </div>

      <!-- Invoice Details Table -->
      <div style="margin-bottom: 15mm;">
        <table style="width: 100%; border-collapse: collapse;">
          <thead>
            <tr style="background-color: #F1F5F9;">
              <th style="text-align: left; padding: 12px 16px; font-weight: 600; font-size: 13px; border-bottom: 1px solid #E2E8F0;">항목</th>
              <th style="text-align: right; padding: 12px 16px; font-weight: 600; font-size: 13px; border-bottom: 1px solid #E2E8F0;">금액</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td style="padding: 16px; font-size: 13px; border-bottom: 1px solid #E2E8F0;">서비스 이용료</td>
              <td style="padding: 16px; text-align: right; font-size: 13px; font-weight: 500; border-bottom: 1px solid #E2E8F0;">${formatCurrency(record.amount)}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- Total Amount -->
      <div style="display: flex; justify-content: space-between; align-items: center; padding-top: 10mm; border-top: 2px solid #5B4FFF; margin-bottom: 15mm;">
        <span style="font-size: 18px; font-weight: bold;">합계 금액</span>
        <span style="font-size: 24px; font-weight: bold; color: #5B4FFF;">${formatCurrency(record.amount)}</span>
      </div>

      <!-- Footer Note -->
      <div style="padding-top: 10mm; border-top: 1px solid #E2E8F0; font-size: 11px; color: #666;">
        <p style="margin: 0 0 4px 0;">감사합니다.</p>
        <p style="margin: 0;">인보이스 수령 후 30일 이내에 입금 부탁드립니다.</p>
      </div>

      <!-- Page Number -->
      <div style="position: absolute; bottom: 10mm; left: 0; right: 0; text-align: center; font-size: 10px; color: #999;">
        Page ${index + 1} of ${total}
      </div>
    </div>
  `
}

export async function generateInvoicePDF(records: InvoiceData[]) {
  const pdf = new jsPDF({
    orientation: "portrait",
    unit: "mm",
    format: "a4",
  })

  for (let i = 0; i < records.length; i++) {
    const record = records[i]

    // Create a temporary div to render the HTML
    const tempDiv = document.createElement("div")
    tempDiv.style.position = "absolute"
    tempDiv.style.left = "-9999px"
    tempDiv.innerHTML = createInvoiceHTML(record, i, records.length)
    document.body.appendChild(tempDiv)

    try {
      // Convert HTML to canvas
      const canvas = await html2canvas(tempDiv.firstChild as HTMLElement, {
        scale: 2, // Higher quality
        useCORS: true,
        logging: false,
        backgroundColor: "#ffffff",
      })

      // Get canvas dimensions
      const imgWidth = 210 // A4 width in mm
      const imgHeight = (canvas.height * imgWidth) / canvas.width

      // Convert canvas to image
      const imgData = canvas.toDataURL("image/png")

      // Add new page if not the first invoice
      if (i > 0) {
        pdf.addPage()
      }

      // Add image to PDF
      pdf.addImage(imgData, "PNG", 0, 0, imgWidth, imgHeight)
    } finally {
      // Clean up
      document.body.removeChild(tempDiv)
    }
  }

  // Save the PDF
  const fileName = `invoices_${new Date().toISOString().split("T")[0]}.pdf`
  pdf.save(fileName)
}
