"use client"

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Download, X } from "lucide-react"
import { ScrollArea } from "@/components/ui/scroll-area"

interface Record {
  id: string
  company: string
  amount: number
  contactName: string
  contactEmail: string
}

interface InvoicePreviewDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  records: Record[]
  onDownload: () => void
}

const COMPANY_INFO = {
  name: "(주)GS",
  address: "서울특별시 강남구 논현로 508 GS타워",
  bank: "우리은행 982-018207-01-002 ㈜지에스",
}

export function InvoicePreviewDialog({ open, onOpenChange, records, onDownload }: InvoicePreviewDialogProps) {
  const today = new Date().toLocaleDateString("ko-KR", {
    year: "numeric",
    month: "long",
    day: "numeric",
  })

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("ko-KR").format(amount) + "원"
  }

  const handleDownloadClick = () => {
    onDownload()
    onOpenChange(false)
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-4xl max-h-[90vh] p-0">
        <DialogHeader className="px-6 pt-6 pb-4 border-b">
          <div className="flex items-center justify-between">
            <DialogTitle className="text-2xl">인보이스 미리보기</DialogTitle>
            <Button variant="ghost" size="icon" onClick={() => onOpenChange(false)}>
              <X className="h-5 w-5" />
            </Button>
          </div>
          <p className="text-sm text-muted-foreground mt-2">총 {records.length}개의 인보이스가 생성됩니다</p>
        </DialogHeader>

        <ScrollArea className="h-[calc(90vh-180px)] px-6">
          <div className="space-y-8 py-4">
            {records.map((record, index) => (
              <div key={record.id} className="bg-white border rounded-lg p-8 shadow-sm">
                {/* Invoice Header */}
                <div className="flex items-center justify-between mb-6 pb-4 border-b-2 border-primary">
                  <h2 className="text-3xl font-bold text-primary">INVOICE</h2>
                  <div className="text-right text-sm text-muted-foreground">
                    <div className="font-semibold">발행일</div>
                    <div>{today}</div>
                  </div>
                </div>

                {/* From and To Information */}
                <div className="grid grid-cols-2 gap-8 mb-8">
                  <div>
                    <h3 className="text-sm font-bold text-muted-foreground mb-3">FROM</h3>
                    <div className="space-y-1 text-sm">
                      <div className="font-semibold text-base">{COMPANY_INFO.name}</div>
                      <div className="text-muted-foreground">{COMPANY_INFO.address}</div>
                      <div className="text-muted-foreground mt-2">
                        <span className="font-medium">입금계좌:</span> {COMPANY_INFO.bank}
                      </div>
                    </div>
                  </div>
                  <div>
                    <h3 className="text-sm font-bold text-muted-foreground mb-3">TO</h3>
                    <div className="space-y-1 text-sm">
                      <div className="font-semibold text-base">{record.company}</div>
                      <div className="text-muted-foreground">
                        <span className="font-medium">담당자:</span> {record.contactName}
                      </div>
                      <div className="text-muted-foreground">{record.contactEmail}</div>
                    </div>
                  </div>
                </div>

                {/* Invoice Details Table */}
                <div className="mb-6">
                  <table className="w-full">
                    <thead>
                      <tr className="bg-slate-100 border-b">
                        <th className="text-left py-3 px-4 font-semibold text-sm">항목</th>
                        <th className="text-right py-3 px-4 font-semibold text-sm">금액</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr className="border-b">
                        <td className="py-4 px-4 text-sm">서비스 이용료</td>
                        <td className="py-4 px-4 text-right text-sm font-medium">{formatCurrency(record.amount)}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>

                {/* Total Amount */}
                <div className="flex justify-between items-center pt-4 border-t-2 border-primary">
                  <span className="text-lg font-bold">합계 금액</span>
                  <span className="text-2xl font-bold text-primary">{formatCurrency(record.amount)}</span>
                </div>

                {/* Footer Note */}
                <div className="mt-8 pt-4 border-t text-xs text-muted-foreground space-y-1">
                  <p>감사합니다.</p>
                  <p>인보이스 수령 후 30일 이내에 입금 부탁드립니다.</p>
                </div>

                {/* Page indicator for multiple invoices */}
                {records.length > 1 && (
                  <div className="text-center text-xs text-muted-foreground mt-4">
                    Page {index + 1} of {records.length}
                  </div>
                )}
              </div>
            ))}
          </div>
        </ScrollArea>

        <DialogFooter className="px-6 py-4 border-t bg-slate-50">
          <Button variant="outline" onClick={() => onOpenChange(false)}>
            취소
          </Button>
          <Button onClick={handleDownloadClick} className="bg-primary hover:bg-primary/90">
            <Download className="mr-2 h-4 w-4" />
            PDF 다운로드
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}
